@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
    .tw-my-gradient {
        background: linear-gradient(0deg,#eb0b0b29,#00000029);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .tw-shadow-main {
        box-shadow: var(--shadow-theme);
    }
}